*,
*:before,
*:after {
    box-sizing: border-box;
}

img {
    max-width: 100%;
    height: auto;
    vertical-align: middle;
}

.header {
    background-color: #aa0601;
    color: white;
    margin: 0 auto;
    padding: 0 88px;
}

@media(max-width:768px) {
    .header {
        padding: 0 39px;
    }
}

@media(max-width:375px) {
    .header {
        display: flex;
        flex-direction: column;
    }

}

.header-menu {
    display: flex;
    flex-wrap: wrap;
    column-gap: 64px;
}

@media(max-width:768px) {
    .header-menu {
        column-gap: 40px;
    }
}


@media(max-width:375px) {
    .menu-item {
        width: 50%;
    }
}

.logo {
    padding: 30px 0;
}

.logo a {
    background: url(../images/logo.png);
    background-size: contain;
    text-indent: 101%;
    overflow: hidden;
    display: block;
    white-space: nowrap;
    width: 5rem;
    height: 3.04rem;
}


.menu-item {
    font-size: 1.25rem;
    line-height: 1.875rem;
    text-align: center;
}

.menu-item a {
    text-decoration: none;
    color: white;
    display: block;
    padding: 10px 0px;
}

.footer {
    max-width: 100%;
    height: 332px;
    background-color: #aa0601;
    margin: 0 auto;
    padding-left: 405px;
}

.footer-menu {
    font-size: 1rem;
    line-height: 1.5;
    text-align: center;
}

footer li a {
    text-decoration: none;
    color: white;
}

.menu-contact {
    padding-top: 2.75rem;
}

.media-icon {
    padding-top: 3rem;
    padding-left: 390px;
}

.media-icon li {
    padding-right: 2rem;
}

.contact {
    font-size: 2.25rem;
    line-height: 1.5;
}

.contact img {
    padding-right: 1.25rem;
}

.contact-tele {
    padding-bottom: 1rem;
}

.contact-email {
    padding-bottom: 1.5rem;
    border-bottom: 1px solid white;
}

.private {
    font-size: 1rem;
    line-height: 1.5;
    color: white;
    padding-top: 2rem;
    padding-bottom: 39px;
}

.private-list1 {
    padding-right: 38.375rem;
}