*,
*::before,
*::after {
    box-sizing: border-box;
}

img {
    max-width: 100%;
    height: auto;
    vertical-align: middle;
}


//綜合設定


//flex
.d-f {
    display: flex;
}

.jcsb {
    display: flex;
    justify-content: space-between;
}

.align-center {
    display: flex;
    align-items: center;
}

.align-end {
    display: flex;
    align-items: flex-end;
}

.justify-center {
    display: flex;
    justify-content: center;
}

.justify-end {
    display: flex;
    justify-content: flex-end;
}

.space-between {
    display: flex;
    justify-content: space-between;
}

.flex-column {
    display: flex;
    flex-direction: column;
}

//間距
.menu-list {
    padding-right: 3rem;
}

//文字
body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Noto Sans TC', 'Microsoft JhengHei', Roboto, 'Helvetica Neue', Arial, sans-serif;
    ;
}

//RWD
.container {
    max-width: 1140px;
    margin: 0 auto;
    padding: 0 15px;
}

//平板
@media(max-width:768px) {
    .container {
        max-width: 720px;
    }
}

//手機
@media(max-width:375px) {
    .container {
        max-width: 345px;
    }
}